*{
  font-family:Arial, Helvetica, sans-serif;
}

body{
  padding:60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width:100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content:center;
  border: 0.2px solid #f3f3f3;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}